/** This module controls the opening and closing, since they do not involve any rendering through React */

export function openSlidingOverlay(elemId:string) : void {
    let pane:HTMLElement | null = document.getElementById(elemId) ;
    if(!pane) { return }

    let direction = pane.dataset.slideOpenDirection ;
    if(direction === "left") {
        pane.style.left = "0px" ; 
    } 
    if(direction === "top") {
        pane.style.top = "0px" ; 
    } 
}

export function closeSlidingOverlay(elemId:string) : void {
    let pane:HTMLElement | null = document.getElementById(elemId) ;
    if(!pane) { return }

    let direction = pane.dataset.slideOpenDirection ;
    if(direction === "left") {
        pane.style.left = pane.offsetWidth.toString() + "px"; ; 
    }
    if(direction === "top") {
        pane.style.top = pane.offsetHeight.toString() + "px"; ; 
    }    
}

export function makeOverlayCloser(elemId:string) {
    return (() => {
        closeSlidingOverlay(elemId) ;
    }) ;
}