import React, { Component} from 'react'
import './css/nearbydrinkslist.css' ;

/* type props and state*/
// https://bobbyhadz.com/blog/react-property-does-not-exist-on-type-readonly
class NearbyDrinksList extends Component
	<
	{drinks:any, appCallbackDrinkSelected:any}, 
	{selectedDrinkId: Number, drinksType: any}
	> 
	{
	styleUl = {
		listStyle: "none",
		padding: "5px",
		// listStyleType: "square",
	}

	/*
	styleLi = {
		backgroundColor: "#70af70",
		border: "solid thin #00a000",
		marginBottom: "5px",
		paddingLeft: "3px",
		paddingBottom: "3px",
		cursor: "pointer",
		borderRadius: "3px",
		fontSize: "12px"
	}

	styleLiSelected = this.styleLi ;
	*/


	constructor(props:any) {
		super(props) ;

		this.state = {
			selectedDrinkId: -1,
			drinksType: props.drinksType,
		}
	}

	render() {
		// console.log("nearbydrinkslist::theApp", this.app) ;

		let listItems =
			this.props.drinks.map((drink:{[key:string]:any}) =>
				<DrinkListItem 
					drink={drink} 
					onClick={this.onClick.bind(this)} 
					// drinkTypes = {this.app.drinkTypes }
					isSelected = { drink.id === this.state.selectedDrinkId}
					key={drink.id}
					/>
			) ;

		return(
			<div id="search_drinks_list_wrapper">
				Nearby Drinks List 
				{/* {this.brandsDict} */}
				<ul className="NearbyDrinksList" style={this.styleUl}>
				{listItems}
				</ul>
			</div>
		)
	}

	onClick(ev:Event) {
		let target:HTMLElement = ev.currentTarget as HTMLElement ;
		let drinkId:any = target.dataset.drinkId;
		console.log("Drink Selected from nearbylist", drinkId) ;
		this.setState({
			selectedDrinkId: parseInt(drinkId)
		})
		this.props.appCallbackDrinkSelected(drinkId) ;
	}
}

const DrinkListItem = ({drink, onClick, isSelected}
:{
	drink:any, onClick:any, isSelected:Boolean
}
)=> {
	//the CSS for these DrinkType_ classes is dynamically generated by App::buildDrinkTypeStylesheet()
	var className = "DrinkType_" + drink.drink_type ;
	if(isSelected) {
		className += " Selected" ;
		// console.log(className) ;
	};

	return (
		<li 
		key={drink.id} 
		onClick={onClick} 
		data-drink-id={drink.id} 
		data-drink-name={drink.name}
		className={className}
		>
			<span>&#9632;</span>&nbsp;{drink.name}
		</li>
	) ;
}


export default NearbyDrinksList ;