import React, { CSSProperties, Component, ChangeEventHandler } from 'react';
import { settings } from '../site_settings';

type VendingMachine = {
	id:number,
	location:[number, number],
	brand:string
} ;

const VendingMachineDetails = ({machine}:{[key:string]:any}) => {
	const styleTextRegion:CSSProperties = { padding: "10px" };

	//let machine = this.props.machine ;
	// const editMachineUrl = settings.apiHost + "/contribute/" + machine.id + "/change/#/machine"
	return (
		<div>
			<UserMenu machine={machine}
			/>
			<div style={styleTextRegion}>
				{!machine.is_not_vending_machine && (<h5>{machine.brand ? machine.brand : ""}</h5>)}
				<h5>{machine.place_description}</h5>
				<h5>{machine.date}</h5>
				<h5>{machine.configuration}</h5>
			</div>
			<img id="machine_image" src={settings.mediaHost + machine.medium_image_url} alt="" style={{ width: "100%" }} />
			{!machine.is_not_vending_machine && (
				<div style={styleTextRegion}>
					<DrinksList drinks={machine.vended_drinks} />
				</div>
			)}
		</div>
	)
}

const UserMenu = ({ machine }:{[key:string]:any}) => {
	const style:CSSProperties = {
		backgroundColor: "#411790",
		color: "yellow",
		padding: "0.5em",
		fontSize: "12px",
		textTransform: "uppercase",
		margin: "0"
	}
	const editMachineUrl = settings.apiHost + "/contribute/" + machine.id + "/change/#/machine"

	return (<div style={style}>
		<a href={editMachineUrl} target="_blank" rel="noreferrer">Edit from browser</a>
	</div>)
}

const DrinksList = ({ drinks }:{[key:string]:any}) => {
	const style:CSSProperties = {
		listStyle: "none",
		marginLeft: "0px",
		padding: "0px"
	};

	return (drinks && (
		<div>
			<h4>Vended Drinks</h4>
			<ul style={style}>
				{drinks.map((drink:any) => <Drink drink={drink} key={drink.id} />)}
			</ul>
		</div>
	));
}

const Drink = ({ drink }:{[key:string]:any}) => {
	return (
		<li className={"DrinkType_" + drink.drink_type}>
			<span>&#9632;</span>&nbsp;
			{(drink.product_url ? <a href={drink.product_url} target={"_blank"} rel="noopener noreferrer">{drink.drink_name}</a> : drink.drink_name)}&nbsp;
			{drink.volume}ml@&yen;{drink.price}
		</li>
	)
}


//TODO: convert to a functional component?
class VendingMachineDetailsForm extends Component <{
	machineBrandsList: any[], 
	machineConfigurationsList: any[], 
	onBrandSelected:ChangeEventHandler,
	onConfigurationSelected: ChangeEventHandler
},{}> {
	// constructor(props) {
	//   super(props) ;
	// }

	// for -> htmlFor
	// https://stackoverflow.com/questions/59924585/im-getting-an-error-using-react-invalid-dom-property-for-did-you-mean-htm

	render() {
		return (
			<div>
				<label htmlFor="machine_location">Location:</label>
				<input id="text_location" type="text"></input><br />

				<label htmlFor="select_machine_brand">Brand:</label>
				<select name="machine_brand" id="select_machine_brand" onChange={this.props.onBrandSelected}>
					{this.props.machineBrandsList.map((brand) => <option value={brand.pk} key={brand.pk}>{brand.name}</option>)}
				</select><br />

				<label htmlFor="select_machine_configuration">Configuration:</label>
				<select name="machine_configuration" id="select_machine_configuration" onChange={this.props.onConfigurationSelected}>
					{this.props.machineConfigurationsList.map((conf) => <option value={conf.pk} key={conf.pk}>{conf.__str__}</option>)}
				</select><br />

				Owner:
			</div>
		)
	}

	onBrandSelected() {
		console.log("onBrandSelected");
	}

	onConfigurationSelected() {
		console.log("onConfigurationSelected");
	}

}


export { VendingMachineDetails, VendingMachineDetailsForm };
export type { VendingMachine };