import React, {Component, CSSProperties, ReactNode} from 'react'
import './css/slidingoverlay.css' ;

type SlidingOverlayProps = {
    openDirection:string,
    children:ReactNode[],
    id: string
}

class SlidingOverlay extends Component
<SlidingOverlayProps, {}> 
{
    style:CSSProperties = {
        position: "fixed",
        zIndex: "1",
        transition: "0.5s",
        overflowY: "auto"
    }

    constructor(props:SlidingOverlayProps) {
        super(props) ;

        this.style.width = window.innerWidth ;
        this.style.height = window.innerHeight ;
        if(props.openDirection === "left") {
            this.style.left = this.style.width.toString() + "px" ; 
        }
        if(props.openDirection === "top") {
            this.style.top = this.style.height.toString() + "px" ; 
        }
    }

    render() {
        var className = "SlidingOverlay" ;

        return (
            <div className={className} style={this.style} id={this.props.id} data-slide-open-direction={this.props.openDirection}>
                {this.props.children}
            </div>
        ) ;
    }
}

export default SlidingOverlay ;