import React, {Component} from 'react';
import { settings } from './site_settings' ;

import {
	isMobile
  } from "react-device-detect";
  
import { VendingMachineDetails } from './components/vendingmachine';
import NearbyDrinksList from './components/nearbydrinkslist' ;
//This import means the default export should be imported as SagasodaGoogleMap, 
//and export named InfoWindowContents should be imported as that same name 
import SagasodaGoogleMap from './components/sagasoda_map' ;
import {InfoPaneHeader} from './components/infopane' ;
import Palette from './components/palette' ;
import UploadConfirmation from './components/uploadconfirm' ;
import SlidingOverlay from './components/slidingoverlay' ;
import { closeSlidingOverlay,  openSlidingOverlay, makeOverlayCloser} from './modules/slidingoverlay' ;
import './App.css' ;

/** General notes:
 * 
 * The drinkTypes and machineBrands dictionaries are fetched and set just once, but 
 * I made them state variables so they can be inspected with React Dev Tools
 */
class App extends Component {
	styleInfoPane = {
		height: "100%",
		overflow: "scroll",
	}

	styleLeftPane = {
		overflowX: "hidden",
		position: "relative",
		height: "100%"
	}

	constructor(props) {
		super(props);
		//state should contain only variables that need to be re-read on render
		this.state =  {
			drinksInMachines: [],	//nearby drinks list

			isViewingMachine: false,
			currentInfoPaneComponent: NearbyDrinksList,
			isFetching: false,
			isGeolocationOn: false,
			initialCenter: null,
			loadedImageFiles : [],
			machineBrands :null,
			drinkTypes : null,
			machineConfigurations: null,
			screenOrientation: window.screen.orientation 
		}
		this.cachedMarkers = new Set() ;
		this.selectedMachineInfo = null ;

		this.defaultCenter = settings.defaultCenter ;

		this.isGeolocationCapable = false ;
		//fetch resources from server

		this.fetchMachineBrandsDict() ;
		this.fetchDrinkTypesDict() ;
		this.fetchMachineConfigurationsList() ;
	}

	render() {
		let paneClassName = this.state.isViewingMachine ? "MachineDetails" : "NearbyDrinks" ;
		var paneComponent = null ;

		let uploadConfirmationComponent = 			
			<UploadConfirmation 
				loadedImageFiles={this.state.loadedImageFiles} 
				// nearbyMachines={this.state.nearbyMachines} 
				panToLocationCallback={this.google && this.showUploadedImageLocation.bind(this)}
				// mapComponent={this.mapComponent}
				machineBrandsDict={this.state.machineBrands}
				machineConfigurationsList={this.state.machineConfigurations}
				/> ;

		let nearbyDrinksListComponent = 
			<NearbyDrinksList
				drinks={this.state.drinksInMachines} 
				appCallbackDrinkSelected={this.onDrinkSelected.bind(this)} 
				app={this}
				/> ;
		
		let vendingMachineDetailsComponent = 
			<VendingMachineDetails 
				machine={this.selectedMachineInfo} 
				/> ;

		switch(this.state.currentInfoPaneComponent) {
			case UploadConfirmation:
				paneComponent = uploadConfirmationComponent ;
				break ;
			case NearbyDrinksList:
				paneComponent = nearbyDrinksListComponent ;
				break ;
			case VendingMachineDetails:
				paneComponent = vendingMachineDetailsComponent ;
				break ;
			default:
				paneComponent = null ;				
		}

		return (	//(this.isAllDictsLoaded()) &&
			<div id="sagasoda_app_component">
				{ !isMobile && (
				<div id="info_pane" className={paneClassName} style={this.styleInfoPane}>
					<div id="search_or_machine_pane">
						<InfoPaneHeader 
							// isViewingMachine={this.state.isViewingMachine} 
							currentInfoPaneComponent={this.state.currentInfoPaneComponent}
							modeCallback={this.showNearbyDrinksList.bind(this)} 
							/>
						
						<div>
							{paneComponent}
						</div>
					</div>
				</div>
				)}


				{ isMobile && 
					<SlidingOverlay 
						id="machine_info_pane"
						openDirection="left" 
						>
						<div className="PanelHeading">
			                <img src={"images/ui/buttons/slide-right.png"} onClick={makeOverlayCloser("machine_info_pane")} alt="Close Machine Info Panel"/>
            			</div>
						{ this.selectedMachineInfo &&
						(<div>{ vendingMachineDetailsComponent }
						</div>
						)}
					</SlidingOverlay>
				}
				
				{ isMobile && 
					<SlidingOverlay 
						id="upload_confirm_info_pane"
						openDirection="top" 
						// openMachineInfoOverlayCallback={openMachineInfoOverlay}
						>
						<div className="PanelHeading">
			                <img src={"images/ui/buttons/slide-down.png"} onClick={makeOverlayCloser("upload_confirm_info_pane")} alt="Close Upload Panel"/>
            			</div>
						{ uploadConfirmationComponent }
					</SlidingOverlay>
				}


				{ isMobile && 
					<SlidingOverlay 
						id="drinks_list_pane"
						openDirection="top" 
						>
						<div className="PanelHeading">
			                <img src={"images/ui/buttons/slide-down.png"} onClick={makeOverlayCloser("drinks_list_pane")} alt="Close Nearby Drinks List Panel"/>
            			</div>
						<div>
							<div>{ nearbyDrinksListComponent }</div>
						</div>
					</SlidingOverlay>
				}

				<div id="left_pane" style={this.styleLeftPane}>
					{/* <div id="map" style={{width:"100%", height:"100%"}}></div> */}
					{(this.state.initialCenter && this.state.machineBrands &&
					<SagasodaGoogleMap 
						app={this} 
						notifyAppMarkerSelected={this.onMarkerSelected.bind(this)}
						notifyAppVisibleMarkersChanged={this.onVisibleMarkersChanged.bind(this)}
						initialCenter = {this.state.initialCenter}
						// passSelfCallback = { this.passSGM.bind(this) }
						machineBrands = {this.state.machineBrands}
						registerMapComponent = { component => this.mapComponent = component }
						registerGoogleObject = { google => this.google = google }
						registerGoogleMap = { map => this.map = map }
						isGeolocationOn = {this.state.isGeolocationOn}
						isViewingMachine = {this.state.isViewingMachine}
						/>
					)}
					<Palette 
						isFetching={this.state.isFetching}
						isGeolocationOn={this.state.isGeolocationOn}
						filesLoadedCallback={this.processFiles.bind(this)}
						nearbyDrinksListCallback={this.showNearbyDrinksList}
						appCallbackShowUserGeolocation={this.showUserGeolocation.bind(this)}
					/>
				</div>
			</div>
		);  
	}

	//////////////////////////////
	// React lifecycle functions

	componentDidMount() {
		/**
		 * Decide where to center the map before it gets created
		 * It can be the location of a specified machine,
		 * or geolocation of the user.
		 * In all cases, we set the state variable initialCenter
		 * to trigger a render on the SagasodaGoogleMap component,
		 * which will not render unless it has changed
		 */
		console.log("App:componentDidMount") ;
		let myHash = window.location.hash ;
		let myUrlHashFields = myHash.split("/") ;
	
		if (myUrlHashFields[1] === "viewing") {
			let initialMachineId = myUrlHashFields[2] ;
			this.fetchMachineInfo(initialMachineId) ;//this will asynchronously set initialCenter
			// if(isMobile) {
			// 	openMachineInfoOverlay() ;
			// }
		} else {
			if(navigator.geolocation) {
			// if((settings.isGeolocationMocked) || (window.location.protocol === "https:" && navigator.geolocation)) {
				console.log("Device has geolocation capability.") ;

				this.isGeolocationCapable = true ;

				navigator.geolocation.getCurrentPosition(
					(position) => {
						console.log("Geolocation obtained:", position.coords.latitude, ":", position.coords.longitude) ;
						this.setState({
							initialCenter: {lat:position.coords.latitude, lng:position.coords.longitude},
							isGeolocationOn: true
						});
					},
					(position) => {
						alert("Geolocation is available, but could not get a fix. Using default location.") ;
						this.setState({
							initialCenter: this.defaultCenter
						});
					});
			} else {
				console.log("Device does not have geolocation capability.") ;
				this.setState({
					initialCenter: this.defaultCenter
				});
			}
		}

		let appComponent = this ;

		window.addEventListener("orientationchange", function(ev) {
			console.log("onorientationchange", ev, window.screen.orientation);
			appComponent.setState ({
				screenOrientation: window.screen.orientation
			});
		}) ;
	}

	//The arrow notation binds this to this object, rather than the object
	//that the function is set as a handler to
	/*
	onSelectionChanged = ev => {
		console.log("App detects that selection changed.", ev.currentTarget.value) ;
		this.selectMachine(ev.currentTarget.value) ;
	}
	*/

	// onFileDroppedOnPalette(ev) {
	// 	console.log("onFileDroppedOnPalette(); event:", ev) ;
	// 	dropHandler(ev) ;
	// } 

	// onFilesSelectedFromDialog() {
	// 	console.log("onFilesSelectedFromDialog") ;
	// 	clickedUploadButton(this.processFiles);
	// }

	///////////////////////////////////////////////////
	//functions to update the UploadConfirm component

	processFiles(files) {
		console.log("processFiles", files) ;
		openSlidingOverlay("upload_confirm_info_pane") ;
		this.setState({
			// isConfirmingUpload: true,
			loadedImageFiles: files,
			currentInfoPaneComponent: UploadConfirmation
		}) ;
	}

	showUploadedImageLocation(geoInfo) {
		console.log("showUploadedImageLocation") ;

		let fakeMachine = {
			location : {
				"lat":geoInfo.latitude,
				"lng":geoInfo.longitude,
			},
			"orientation": geoInfo.direction
		}
		this.mapComponent.putOrientationMarker(fakeMachine, this.map, true) ;
	}

	
	///////////////////////////////////////////////////
	//functions to update the MachineInfo component

	selectMachine(machineId) {
		this.fetchMachineInfo(machineId) ;
		if(isMobile) {
			openSlidingOverlay("machine_info_pane") ;
			//			openMachineInfoOverlay() ;
		}
	}

	showMachineInfo(machineInfo) {
		this.setState({
			// selectedMachineInfo: data, 
			isViewingMachine:true,
			initialCenter: { lat:machineInfo.location.lat, lng:machineInfo.location.lng },
			isFetching: false,
			currentInfoPaneComponent: VendingMachineDetails
		}) ;
	}
	
	/////////////////////////////////////////////
	// functions called from, and which update the NearbyDrinksList component
	//called by: NearbyDrinksList
	//updates: map via fetch
	onDrinkSelected(drinkId) {
		// let drinkId = ev.currentTarget.dataset.drinkId;
		console.log("onDrinkSelected from nearbylist", drinkId) ;
		this.fetchMachinesVendingDrink(drinkId) ;
		if(isMobile) {
			closeSlidingOverlay("drinks_list_pane") ;
		}
	}

	//called by: Palette, VendingMachineInfo
	//updates state to: NearbyDrinksList
	showNearbyDrinksList() {
		// console.log("showNearbyDrinksList") ;
		if(isMobile) {
			openSlidingOverlay("drinks_list_pane") ;
//			openDrinksListOverlay() ;
		}
		this.setState({ 
			isViewingMachine:false,
			currentInfoPaneComponent : NearbyDrinksList
		}) ;
	}

	//////////////////////////////////////////////////
	// functions called from the map
	// functions to update the map, without using React

	//called by map
	//updates state to: NearbyDrinksList
	onVisibleMarkersChanged(machines) {
		this.fetchDrinksInMachines(machines) ;
		this.updateMarkersCache(machines) ;
	}

	/**
	 * called back by Palette
	 */
	showUserGeolocation() {
		console.log("showUserGeolocation") ;

		let mapComponent = this.mapComponent ;

		navigator.geolocation.getCurrentPosition(
			function(position) {
				console.log("Geolocation updated:", position.coords.latitude, ":", position.coords.longitude) ;
				mapComponent.addUserLocationMarker(position.coords.latitude, position.coords.longitude) ;
			},
			function() {
				alert("Geolocation is available, but could not get a fix. Please try again later.") ;
			});
	}

	//called by: map
	//updates: VendingMachine
	onMarkerSelected(markerId) {
		this.selectMachine(markerId) ;
	}

	//functions that are called from components (passed as props) and 
	//dispatched to the map 


	/*
	promptUpload() {
		console.log("promptUpload") ;
		let chooseFileButton = document.querySelector("#search_or_machine_pane input") ;
		chooseFileButton.click() ;
		// clickedUploadButton() ;
	}
	*/




//   onChange() {
// 	  console.log("Some child changed", ) ;
//   }



	//////////////////////////
	// functions to fetch initial data dictionaries.
	// These are called from the App component constructor 
	fetchMachineBrandsDict() {
		let url = settings.apiHost + '/api/machinebrands/?as_dict' ;
		fetch(url)
		.then(res => res.json())
		.then((data) => {
			this.setState({ machineBrands : data }) ;
		})
		.catch(console.log)		
	}

	fetchDrinkTypesDict() {
		let url = settings.apiHost + '/api/drinktypes/?as_dict' ;
		fetch(url)
		.then(res => res.json())
		.then((data) => {
			this.buildDrinkTypeStylesheet(data) ;
			this.setState({ drinkTypes : data }) ;
		})
		.catch(console.log)		
	}

	fetchMachineConfigurationsList() {
		let url = settings.apiHost + '/api/machineconfigurations/' ;
		fetch(url)
		.then(res => res.json())
		.then((data) => {
			this.setState({ machineConfigurations : data }) ;
		})
		.catch(console.log)		
	}

	isAllDictsLoaded() {
		// return true ;
		return (
			this.state.machineBrands && 
			this.state.drinkTypes &&
			this.stats.machineConfigurations &&
			true
			) ;
	}


	/**
	 * Dynamically build a stylesheet for the colored bullets in front of drinks
	 * indicating drink type.
	 */
	buildDrinkTypeStylesheet(drinkTypes) {
		let style = document.createElement("style") ;
		style.type = "text/css" ;
		style.id = "drink_type_colors" ;

		var contents = "" ;

		for(var key in drinkTypes) {
			let def = "li.DrinkType_" + key + " span { color:" + drinkTypes[key].color + " ; }" ;
			contents += def ;
		} 
		style.innerHTML = contents ;

		document.getElementsByTagName('head')[0].appendChild(style);
	}


	fetchMachineInfo(id) {
		if(!id) { return ; }
		let url = settings.apiHost + '/api/vendingmachines/' + id + '/?output=read_only' ; 
		window.location.hash = "#/viewing/" + id ;
		console.log("Fetching machine details:", url) ;
		this.setState({ isFetching: true}) ;

		fetch(url)
		.then(res => res.json())
		.then((data) => {
			this.selectedMachineInfo = data ;
			this.showMachineInfo(data) ;
			// console.log("Fetching:", this.state.isFetching) ;
		})
		.catch(console.log)
	}


	updateMarkersCache(machines) {
		let cachedMarkers = this.cachedMarkers ;
		var newMarkers =[] ;

		machines.forEach(function(mach) {
			if(!cachedMarkers.has(mach.id)) {
				newMarkers.push(mach) ;
				cachedMarkers.add(mach.id) ;
			} ;
		}) ;

		this.setState({ 
			visibleMarkers: machines,
		}) ;
	}
	/**
	 * This is not related to the map, so it should not do anything
	 * to the markers.
	 * @param {*} machines 
	 */
	fetchDrinksInMachines(machines) {
		let argsStr = (machines.map(m => m.id)).join(",") ;
		let url = settings.apiHost + "/api/drinks/?ouput=minimal&sorted_by=type&in_machines=" + argsStr ;

		fetch(url)
		.then(res => res.json())
		.then((data) => {
			this.setState({ 
				drinksInMachines: data,
			}) ;
		})
		.catch(console.log)
	}

	fetchMachinesVendingDrink(drinkId) {
		let idsStr = this.state.visibleMarkers.map((marker)=>marker.id).join(',') ;
		let url = settings.apiHost +
			"/api/vendingmachines/" + 
			"?output=with_vended_drinks" + 
			"&with_drink=" + drinkId + 
			"&among=" + idsStr ;

		console.log("Fetching machine details:", url) ;
		fetch(url)
		.then(res => res.json())
		.then((data) => {
			this.mapComponent.showInfoWindows(data) ;
		})
		.catch(console.log)
	}
}

export default App;
