export function isBrowserRotatesByExif(elem: HTMLElement) {
    //define a sample image 16 x 8 with a 3 rotation tag
    let dataUrl16x8rot3 = "data:image/png;base64," ;

    //place it in elem with the transform 
    //compare img.getBoundingClientRect().width with img.width
    let img = document.createElement("img") ;
    img.style.transform = "rotate(0.5turn)" ;
    img.src = dataUrl16x8rot3 ;
    elem.append(img) ;
}

