export function brandIconPath(machine: Record<string, any>, brandsDict : Record<string, any>) : string {
	var brandTag:string = "unknown" ;

	if(machine.brand) {
		// brandTag = app.state.machineBrands[machine.brand].tag ;
		brandTag = brandsDict[machine.brand].tag ;
	}

	if(machine.is_not_vending_machine) {
		brandTag = "_not_vendingmachine";
	}

	let iconUrl = "images/ui/machine_icons/" + brandTag + ".png";
	return iconUrl ;
}

export function brandName(machine: Record<string, any>, brandsDict : Record<string, any>) : string {
	// console.log("brandName", machine, brandsDict) ;
	var brandName:string = "unknown" ;

	if (machine.is_not_vending_machine) {
		return(machine.place_description) ;
	}

	if(machine.brand) {
		brandName = brandsDict[machine.brand].name ;
	}
	return brandName ;
}
