import React, { Component} from 'react'
import { VendingMachineDetailsForm } from './vendingmachine' ;
import { brandName } from '../modules/item_dicts' ;
import { degreeSexToDecimal, exifRotationToTurns } from '../modules/exif' ;
import { isBrowserRotatesByExif } from '../modules/exif_rotation' ;
import { getUploadedMachineInfo } from '../modules/upload';
import { settings } from '../site_settings' ;
import './css/uploadconfirm.css' ;

var EXIF = require('exif-js/exif.js') ;

type UploadConfirmationProps = {
    machineBrandsDict:any,
    loadedImageFiles:any[],
    panToLocationCallback: Function,
    mapComponent: Component,
    machineConfigurationsList:any[],
}
class UploadConfirmation extends Component 
<UploadConfirmationProps, {}> 
{
    styleImg = {
        width: "100%",
    }

    machineBrandsList: any[] ;
    isBrowserRotationChecked:Boolean;
    isBrowserRotates:Boolean;

    constructor(props:UploadConfirmationProps) {
        super(props) ;

        this.state = {
            uploadInfo: null,
        }
    
        this.machineBrandsList = [] ;
        this.isBrowserRotationChecked = false ;
        this.isBrowserRotates = false ;

        for(var key in props.machineBrandsDict) {
            this.machineBrandsList.push(props.machineBrandsDict[key]) ;
        }
    }

    render() {
        return(
            <div className="UploadConfirmation">
                <div id="rotation_test_image">
                </div>
                <h3>Files loaded in to browser</h3>
                <div>
                    {this.props.loadedImageFiles.map((file:File) => 
                    <LoadedImage 
                        file={file} 
                        key={file.name} 
                        panToLocationCallback={this.props.panToLocationCallback}
                        mapComponent={this.props.mapComponent}
                        machineBrandsDict={this.props.machineBrandsDict}
                        machineBrandsList={this.machineBrandsList}
                        machineConfigurationsList={this.props.machineConfigurationsList}
                        />)}
                </div>
            </div>
            )
    }

    componentDidMount() {
        let testDiv:HTMLElement | null = document.getElementById("rotation_test_image") ;
        testDiv && isBrowserRotatesByExif(testDiv) ;
    }
}

/** Shows the one image and information. 
 * Loaded means image data has been loaded from the file into the browser, not uploaded to the server. */
class LoadedImage extends Component
<
{
    panToLocationCallback:Function,
    machineBrandsList:any[],
    machineBrandsDict:{[key:string]:any},
    machineConfigurationsList:any[],
    file:File,
    mapComponent:Component
},
{
    imageExifInfo:any, 
    imageUrl: string | ArrayBuffer | null, 
    imageDataUrl: string,
    isImageLacksGeoInfo:Boolean, 
    isUploading: Boolean, 
    isUploadComplete: Boolean, 
    isRemoved:Boolean,
    isEditMachineDetails:Boolean,
    uploadResponseData:{[key:string]:any},
    nearbyMachines:any[],
    newMachineId:string
}>
{
    styleImg = {
        width: "100%",
    }

    file:File ;
    imageUrl:string = "";

    constructor(props:any) {
        super(props) ;

        this.state = {
            imageUrl: "",
            imageDataUrl: "",
            imageExifInfo : null,
            isImageLacksGeoInfo : false,
            isUploading : false,
            isUploadComplete : false,
            isEditMachineDetails : false,
            uploadResponseData: {},
            isRemoved: false,
            nearbyMachines: [],
            newMachineId : ""
        }

        this.file = props.file ;

        // let objectUrl:string = URL.createObjectURL(this.file) ;
        this.imageUrl = URL.createObjectURL(this.file) ;
        this.extractExif() ;
        this.setState({imageUrl : this.imageUrl}) ;
    }

    render() {
        var style:{[key:string]:string} = {
            width: this.styleImg.width,
        }
        
        if(this.state.imageExifInfo) {
            let turns = exifRotationToTurns(this.state.imageExifInfo.imgRotation) ;
            if(turns !== "") {
                style.transform = "rotate(" + turns + "turn)" ;
            }
        }

        if(this.state.isRemoved) { return null }
        
        if(this.state.isUploadComplete) {
            let viewingUrl = "#/viewing/" + this.state.uploadResponseData.machine_id ;
            return(
                <div>
                    <div>
                        <h4>Upload Complete</h4>
                        { this.props.panToLocationCallback &&
                        <button onClick={this.onShowLocationClicked.bind(this)}>Show Location</button>
                        }
                        <a href={viewingUrl} target="_blank" rel="noopener noreferrer">View</a>
                    </div>
                    <div>
                    {this.imageUrl && 
                        <img 
                        style={style} 
                        src={this.imageUrl} 
                        onLoad={this.onImageLoaded.bind(this)}
                        data-exif-rotation={(this.state.imageExifInfo && this.state.imageExifInfo.imgRotation)} 
                        alt={this.file.name}/>
                    }
                    </div>
                </div>
            )
        }   //else...
        
        return(
            <div className="BrowserLoadedImage">
                <div>
                    <h5>{this.props.file.name}</h5>
                    { this.state.isImageLacksGeoInfo ?
                    (<div>
                        <p>This image has no geolocation info so it can not be used.</p>
                        <button onClick={this.onRemoveClicked.bind(this)}>Remove</button>
                    </div>) 
                    :
                    (<div id="uploaded_file_buttons">
                        { this.state.isUploading ?
                        <button>Uploading..</button> :
                        <button onClick={this.onUploadClicked.bind(this)}>Upload</button>
                        }
                        <button onClick={this.onRemoveClicked.bind(this)}>Remove</button>
                        { this.props.panToLocationCallback &&
                        <button onClick={this.onShowLocationClicked.bind(this)}>Show Location</button>
                        }
                        <button onClick={this.onEditMachineDetailsClicked.bind(this)}>Edit</button>
                    </div>)
                    }
                </div>

                <div>
                    { this.imageUrl && 
                    <img 
                        style={style} 
                        src={this.imageUrl as string} 
                        onLoad={this.onImageLoaded.bind(this)}  
                        // data-exif-rotation="foo" 
                        data-exif-rotation={(this.state.imageExifInfo && this.state.imageExifInfo.imgRotation)} 
                        alt={this.file.name}/>
                    }
                </div>
                { this.state.isEditMachineDetails &&
                    <VendingMachineDetailsForm 
                        machineBrandsList={this.props.machineBrandsList}
                        machineConfigurationsList={this.props.machineConfigurationsList}
                        onBrandSelected={this.dummyHandler}
                        onConfigurationSelected={this.dummyHandler}
                    />

                }
                { !this.state.isImageLacksGeoInfo &&
                <div className="NearbyMachines">
                    <h5>Found {this.state.nearbyMachines.length} nearby machines</h5>

                    <div>
                        {this.state.nearbyMachines.filter((machine) => !machine.is_not_vending_machine)
                        .map((machine) => 
                        (<NearbyMachineThumbnail
                            key = {machine.id}
                            machine = {machine}
                            machineBrandsDict = {this.props.machineBrandsDict}
                        />)
                        )}
                    </div>
                </div>
                }
            </div>
            )
    }

    dummyHandler() {
        console.log("Dummy handler") ;
    }

    //button handlers
    onUploadClicked() {
        console.log("Preparing to upload:", this.file) ;
        this.uploadAjax(this.file, this.state.imageExifInfo) ;
    }

    onShowLocationClicked() {
        console.log("mapComponent in uploadconfirm", this.props.mapComponent) ;
        this.props.panToLocationCallback(this.state.imageExifInfo) ;
    }

    onRemoveClicked() {
        URL.revokeObjectURL(this.imageUrl as string) ;
        this.setState({
            isRemoved: true
        })
    }

    onEditMachineDetailsClicked() {
        this.setState({
            isEditMachineDetails: true
        });
    }
    
    onImageLoaded(ev:any) {
        console.log("Image loaded " + ev) ;
    }
    
    adjustTransformedImage(img:any) {
        console.log("ImageLoaded") ;
        console.log("Image without transform -- width:", img.width, "height:", img.height) ;
        console.log("bounding rect", img.getBoundingClientRect()) ;

        if(img.style.transform) {
            console.log("Image was transformed", img.dataset.exifRotation) ;

            if((img.dataset.exifRotation === "6") || (img.dataset.exifRotation === "8")) {
                console.log("Image orientation changed")
                // console.log(img.parentElement) ;
                img.parentElement.style.height = img.width.toString() + "px" ;
                let margin = Math.abs(img.width - img.height) / 2 ;
                img.style.marginTop = margin.toString() + "px" ;
            }
        }
    }


    /**
     * Update the LoadedImage component with location information from the image,
     * and nearby machines
     *
     * As much as I want to pull this out to a module,
     * since EXIF.getData is asynchronous, it's easier to leave it here and 
     * have a lexical reference to the callback (fetchNearbyMachines)
     * instead of passing it to the wrapper function.
     */
    extractExif() {
        let thisComponent:Component = this ;
        let fetchNearbyMachines = this.fetchNearbyMachines.bind(this) ;

        EXIF.getData(this.file, () => {
            let exifLat = EXIF.getTag(this.file, "GPSLatitude") ;
            let exifLng = EXIF.getTag(this.file, "GPSLongitude") ;
            let imgRotation = EXIF.getTag(this.file, "Orientation") ;

            if(exifLat && exifLng) {
                let latitude  = degreeSexToDecimal(exifLat) ;
                let longitude = degreeSexToDecimal(exifLng) ;
                
                let exifLngRef = EXIF.getTag(this.file, "GPSLongitudeRef") ;
                let exifLatRef = EXIF.getTag(this.file, "GPSLatitudeRef") ;

                if (exifLatRef !== 'N') { latitude = 0 - (latitude as number) } ;
                if (exifLngRef !== 'E') { longitude = 0 - (longitude as number) } ;

                let exifDirection = EXIF.getTag(this.file, "GPSImgDirection") ;

                var direction = 0 ;

                if(exifDirection) {
                    direction = Math.round(exifDirection.numerator / exifDirection.denominator) ;
                } 

                fetchNearbyMachines(latitude, longitude) ;

                thisComponent.setState ({
                    isImageLacksGeoInfo: false,
                    imageExifInfo: {
                        latitude: latitude,
                        longitude: longitude, 
                        direction: direction,
                        imgRotation:imgRotation
                    }
                }) ;

                
            } else {
                thisComponent.setState ({
                    isImageLacksGeoInfo: true,
                })
            }
        })
    }

    uploadAjax(file:File, geoInfo:{[key:string]: any}) {
        //https://www.taniarascia.com/how-to-upload-files-to-a-server-with-plain-javascript-and-php/
        
        const formData = new FormData();
        
        formData.append("files[]", file) ;
        formData.append("lat_lng", [geoInfo.latitude, geoInfo.longitude].toString()) ;
        formData.append("orientation", geoInfo.direction) ;
        if(this.state.isEditMachineDetails) {
            let machineInfo = getUploadedMachineInfo() ;
            // formData.append("machine_info", JSON.stringify(machineInfo)) ;
            console.log("Machine info:") ;
            console.log(machineInfo) ;
        }

        // let csrfValue = document.querySelector("#upload_form_wrapper input[name='csrfmiddlewaretoken']").value ;
        let csrfValue = "" ;
        
        // let uploadedCallback = this.uploadedCallback.bind(this) ;

        let thisComponent = this ;

        this.setState({
            isUploading: true
        }) ;
        
        const url = settings.apiHost + "/api/upload/" ;
        fetch(url, {
            method: "POST",
            body: formData,
            headers: {
                // "Content-Type": 'multipart/form-data; boundary=' + boundary,
                // "Content-Disposition": 'form-data; filename="filename.jpg"',
                "X-CSRFToken": csrfValue
            },
        }).then(response => {
            // TODO: check status
            console.log(response) ;
            return response.json()
        }).then(function(data) {
            // console.log(data) ;
            thisComponent.setState({
                isUploadComplete: true,
                isUploading: false,
                uploadResponseData: data,
                //TODO: Replace imageDataUrl with server file URL
            })
            // uploadedCallback(data) ;
        }) ;
    }

	fetchNearbyMachines(lat:Number, lng:Number) {
		const url = settings.apiHost + "/api/vendingmachines?near&lat=" + lat + "&lng=" + lng

		fetch(url)
		.then(res => res.json())
		.then((data) => {
			this.setState({
				nearbyMachines: data
			}) ;	
			console.log("Nearby machines", data) ;
		})
		.catch(console.log)		
	}
}

