function NearbyMachineThumbnail (props:{[key:string]:any}) {
    return (
        <div style={{border:"solid 2px red", float:"left", width:"150px", height:"120px"}}>
            <p>{ brandName(props.machine, props.machineBrandsDict) }<span>&#9746;</span></p>
            { props.machine.id }
            <img 
                src={settings.mediaHost + props.machine.medium_image_url} 
                style={{width:"50px"}} 
                alt="Preview of nearby machine"
                />
        </div>
    ) ;
}

export default UploadConfirmation ;
