/**
 * Files can be uploaded by either clicking on the palette's upload button 
 * or drag-dropping on the palette. There is a hidden file input form
 * Clicking on the palette's upload button
 * fires a click event on the input element. Selecting a file through 
 * the file open dialog then triggers the upload.
 * Drag-dropping instantly triggers the upload
 * 
 */

export function clickedUploadButton(filesSelectedCallback: Function) : void {
    var inputElem : HTMLInputElement = document.getElementById("transient_file_input") as HTMLInputElement ;
    
    if(inputElem) {
        console.log("found transient file input in DOM.") ;        
    } else {
        console.log("creating transient file input") ;
        inputElem = document.createElement("input") as HTMLInputElement ;
        
        inputElem.setAttribute("type", "file") ;
        inputElem.setAttribute("multiple", "multiple") ;

        // inputElem.type = "file" ;
        // inputElem.multiple = "multiple" ;
        // inputElem.id = "transient_file_input" ;
        inputElem.style.display = "none" ;
        inputElem.style.width = "0px" ;
    
        //iOS requires that it be in the DOM
        let rootDiv = document.getElementById("root") ;
        if(rootDiv) {
            rootDiv.append(inputElem) ;
        }
    }

    inputElem.addEventListener("change", function() {
        var selectedFiles:any = [] ;
        
        let files = this.files ;
        console.log("Files have been selected from dialog", files) ;

        if(files) {
            for(var i = 0 ; i < files.length ; i++) {
                let file = files[i] ;
                selectedFiles.push(file) ;
            }
        }

        filesSelectedCallback(selectedFiles) ;
        //processSelectedFiles() ;
    }) ;

    inputElem.click() ;
}


export function droppedFiles(ev: DragEvent, filesSelectedCallback : Function) : void {
    // Prevent default behavior (Prevent file from being opened in browser)
    console.log('Items(s) dropped');
    // ev.preventDefault();

    // var mapPalette: any = document.querySelector(".Palette") ;
    // mapPalette.style.backgroundColor = panelColor ;
    // mapPalette.style.backgroundColor = paletteOriginalBackgroundColor ;

    var selectedFiles: any = [] ;

    if (ev.dataTransfer) {
        var transfer = ev.dataTransfer ;

        if(transfer.items) {
            let items = transfer.items ;
    
            if(items.length < 1) {
                console.log("No items, bailing") ;
                return ;
            } 
    
            console.log("Dropped item is a DataTransferItemList of length", items.length ) ;
            // Use DataTransferItemList interface to access the file(s)
            
            console.log("number of items: ", items.length) ;
    
            //can't use filter() directly on DataTransferItemList
            var fileItems = [] ;
            for (var i = 0; i < items.length; i++) {
                let item = items[i] ;
                if (item.kind === 'file') {
                    fileItems.push(item) ;
                }
            }
            
            selectedFiles = fileItems.map((item) => item.getAsFile()) ;
        } else {
            // Use DataTransfer interface to access the file(s)
            console.log("Dropped item is a DataTransfer") ;
    
            if(transfer.files) {
                if(transfer.files.length < 1) {
                    console.log("No items, bailing") ;
                    return ;
                }
        
                for (var ix = 0; ix < ev.dataTransfer.files.length; ix++) {
                    let file = ev.dataTransfer.files[ix] ;
                    selectedFiles.push(file) ;
                }
            }
        }
    } else {
        console.log("Drag event has no DataTransfer") ;
    }

    filesSelectedCallback(selectedFiles) ;
}

export function generateDataUrl(file : File, callback : Function) : void {
    //we can use URL.createObjectURL(file) instead.
    let reader = new FileReader() ;
    reader.onloadend = function() {
        callback(reader.result) ;
    }
    reader.readAsDataURL(file) ;
}

