/**
 * Scrapes the machine information entered by the user before uploading.
 * @returns A dictionary of machine information
 */
export function getUploadedMachineInfo() : { [key : string] : any} {
    let info : { [key:string] : any } = {} ;

    // var location : string = "";
    let locationElem : HTMLInputElement =  document.getElementById("text_location") as HTMLInputElement;
    if (locationElem) {
        info["location_description"] = locationElem.value ;
    }
   
    let machineBrandElem : HTMLSelectElement = document.getElementById("select_machine_brand") as HTMLSelectElement ;
    if(machineBrandElem) {
        var machineBrandSelection:any = machineBrandElem.options[machineBrandElem.selectedIndex] ; 
        info["machine_brand"] = machineBrandSelection.value ;
    }

    let machineConfigurationElem : HTMLSelectElement = document.getElementById("select_machine_configuration") as HTMLSelectElement ;
    if(machineConfigurationElem) {
        var machineConfigurationSelection:any = machineBrandElem.options[machineBrandElem.selectedIndex] ; 
        info["machine_configuration"] = machineConfigurationSelection.value ;
    }

    return info ;
}