import React, { Component, MouseEventHandler} from 'react'
import NearbyDrinksList from './nearbydrinkslist';

class InfoPaneHeader extends Component<{currentInfoPaneComponent:any, modeCallback:MouseEventHandler}, {}> {
    divStyle = {
        backgroundColor: "#c0e0ff",
        padding: "5px",
        fontSize: "14px"
    }

    imgStyle = {
        cursor: "pointer"
    }

    render() {
		return(
            <div style={this.divStyle}>
                { (this.props.currentInfoPaneComponent !== NearbyDrinksList) && 
                <img 
                    style={this.imgStyle}
                    src={"images/ui/buttons/search-list.png"} 
                    alt="Nearby Drinks List"
                    onClick={this.props.modeCallback}
                    />
                }
            </div>
		)
	}
}

export { InfoPaneHeader } ;