import React, { Component,} from 'react' ;
import { isMobile } from "react-device-detect";
import UploadPanel from './uploadpanel' ;
import {clickedUploadButton, droppedFiles} from '../modules/upload' ;
import './css/palette.css' ;
import { settings } from '../site_settings' ;

// type PaletteProps = {
//     nearbyDrinksListCallback:Function,
//     filesLoadedCallback:Function,
//     isFetching: Boolean,
//     appCallbackShowUserGeolocation:Function,
//     isGeolocationOn: Boolean,
//     onDrop:DragEventHandler
// } ;

class Palette extends Component 
// <PaletteProps,{isDragging:Boolean}> 
{
    stylePalette = {
        // width: "100%",
        backgroundColor: "lime",
        display: "inline-block",
        border: "solid 3px green",
        padding: "3px",
        paddingBottom: "3px",
        pointerEvents: "all"
    }

    styleWrapper = {
        // position: "absolute",
        // top: isMobile ? "65px" : "20px",
        height: "40px",
        left: "0",
        right: "0",
        // textAlign: "center",
        pointerEvents: "none",
    }

    styleWrapperFloating = {
        position: "absolute",
        top: isMobile ? "65px" : "20px",
        height: "40px",
        left: "0",
        right: "0",
        textAlign: "center",
        pointerEvents: "none",
    }

    styleAdArea = {
        float: "right"
    }

    styleFileInput = {
        "display": "none",
        "width": 0
    }

    drinksListCallback ;
    filesLoadedCallback ;
    originalBackgroundColor ;
    
    constructor(props) {
        super(props) ;

        this.state = {
            isDragging : false 
        } ;

        this.drinksListCallback = props.nearbyDrinksListCallback ;
        this.filesLoadedCallback = props.filesLoadedCallback ;
        this.originalBackgroundColor = this.stylePalette.backgroundColor ;
    }

    render() {
        let logoImage = this.props.isFetching ? "images/spinner.gif" : "images/logo-stacked-36.png" ;
        var style = {} ;

        for(var key in this.stylePalette) {
            style[key] = this.stylePalette[key];
        }
        if(this.state.isDragging) {
            style.backgroundColor = "white" ;
        }
        if(!settings.isToolbarFloating) {
            style.width = "100%"
        }

        return (
            <div 
                className="PaletteWrapper" 
                style={settings.isToolbarFloating ? this.styleWrapperFloating : this.styleWrapper}
                >

                <div className="Palette" 
                    style={style} 
                    onDrop={this.onDrop.bind(this)} 
                    onDragOver={this.onDragOver.bind(this)}
                    onDragLeave={this.onDragLeave.bind(this)}
                    title="Drop files on this palette to upload"
                    >
                    <input type="file" multiple={true} id="transient_file_input" style={this.styleFileInput}/>
                    <div className="PaletteButtonWrapper" id="palette_button_wrapper">
                        <a href="/"><img src={logoImage} alt="Home"/></a>
                        { isMobile && (
                        <img src="images/ui/buttons/search-list.png" onClick={this.onDrinksListButtonClicked.bind(this)} alt="Show nearby drinks" />
                        )}
                        <img src="images/ui/buttons/add.png" onClick={this.onUploadButtonClicked.bind(this)} alt="Upload A photograph"/>
                        { this.props.isGeolocationOn &&
                        <img src="images/ui/buttons/user-location-update.png" onClick={this.onUserLocationButtonClicked.bind(this)} alt="Pan to user location"/>
                        }
                        <a href="http://sagasoda.com/doc/about.html" target="_blank" rel="noopener noreferrer">
                            <img src="images/ui/buttons/more.png" alt="More"/>
                        </a>
                        { !settings.isToolbarFloating &&
                        <div id="palette_ad_area" style={this.styleAdArea}>
                            Your ad here
                        </div>
                        }
                    </div>
                    <div id="status_panel">
                    </div>
                </div>
                { false && true &&
                    !isMobile && <UploadPanel /> }
            </div>
        ) ;
    }

    onDragOver(ev) {
        ev.stopPropagation() ;
        ev.preventDefault() ; 
        this.setState({
            isDragging: true    //this triggers a render, which will overwrite the background color to white
        }) ;
        console.log("Dragged over.")
    }

    onDragLeave(ev) {
        ev.stopPropagation() ;
        ev.preventDefault() ; 
        this.setState({
            isDragging: false    //this triggers a render, which use the default background color
        }) ;
        console.log("Drag leave.")
    }

    onDrop(ev) {
        ev.stopPropagation();
        ev.preventDefault() ; 
        droppedFiles(ev, this.filesLoadedCallback) ;
        this.setState({
            isDragging: false
        }) ;
    }
    onDrinksListButtonClicked() {
        this.drinksListCallback() ;
    }
    onUploadButtonClicked() {
        clickedUploadButton(this.filesLoadedCallback) ;
    }
    onUserLocationButtonClicked() {
        this.props.appCallbackShowUserGeolocation() ;
    }
}

export default Palette ;