/**
 * React component with a Google Map compenent from google-maps-react.
 * 
 * We are using a large number of markers, so for efficiency we do not use
 * google-maps-react's scheme which redraws markers on every render. All rendering is
 * disabled after the initial display.
 * 
 * So when other components update the App's state, these updates will not trigger any change
 * in the map. Instead, the app has to call Google Maps APIs directly on the map. To do this,
 * it needs the Google Maps 'google' object and the 'map' objects. So we have to register them
 * with the app. The app provides injector functions for these as props.
 * 
 * registerGoogleObject() is called in the constructor (it receives the google objects from GoogleApiWrapper), 
 * registerGoogleMap() is called in onIdle, which passes the map object.
 * 
 * There is also a similar injector for passing this component object to the App. Its utility is under consideration.
 * It turns out that the app does not need the google or map objects. Rather it should just use the component object, by calling 
 * methods on it. All the Google Maps APIs should be in the component.
 * 
 */
import React, {Component} from 'react';
import { Map, GoogleApiWrapper } from 'google-maps-react';

import { brandIconPath, brandName } from '../modules/item_dicts' ;
import { buildMachineInfoIconButton } from '../modules/sagasoda_map' ;
import './css/infowindow.css' ;

import { settings } from '../site_settings' ;

/**
 * If it uses neither props nor state, it doesn't belong here.
 */
class SagasodaGoogleMap extends Component {
	mapStyles = {
		width: '100%',
		height: '100%',
	};

 	constructor(props) {
		super(props) ;
		this.state = {
			// displayedInfoWindowIds: {},
			initialCenter: props.initialCenter,
			// newlyVisibleMarkers:[],

		}
		
		this.map = null ;

		// this.app = this.props.app ;
		//App callbacks
		// this.passThisComponentToApp = this.props.passSelfCallback ;
		
		// this.notifyAppMapMoved = this.props.notifyAppVisibleMarkersChanged ;
		// this.notifyAppMarkerSelected = this.props.markerCallback ;
		// this.google = this.props.google ;
		
		this.cachedMarkerIds = new Set() 
		this.cachedMarkers = {} ;
		this.openedInfoWindows = [] ;
		this.visibleMarkers = [] ;

		this.orientationMarker = null ;
		this.userLocationMarker = null ;

		this.zoomUpdateLimit = 16 ;
		this.isUpdateDisabledByZoom = false ;
		this.previousZoom = 17 ;
		
		this.props.registerGoogleObject(this.props.google) ;
		console.log("previous zoom", this.previousZoom) ;

		//place some values in global(window) so they can be accessed by straight
		//Google API calls without getting caught up in this
	}
		// binding this to event-handler functions
		// this.onMarkerClick = this.onMarkerClick.bind(this);
		// this.onMapClick = this.onMapClick.bind(this);

	//event names are not properly camelCased
	//https://github.com/fullstackreact/google-maps-react/issues/56#issuecomment-380506511

	render() {
		console.log("SagasodaMap::render()") ;
		return(
		<Map
			id={this.props.id}
			google={this.props.google}
			zoom={this.previousZoom}
			style={this.mapStyles}
			initialCenter={this.state.initialCenter}
			onBounds_changed={this.onBoundsChanged.bind(this)}
			onDragend={this.onDragend.bind(this)}
			onZoom_changed={this.onZoomChanged.bind(this)}
			onTiles_loaded={this.onTilesLoaded.bind(this)}
			onIdle={this.onIdle.bind(this)}
			containerElement={<div className="MapWrapper" />}
			/>
		)
	}

	/////////////////////////////////
	//React overrides
	/**
	 * google-maps-react's Map implements markers and infowindows as child React components
	 * that get re-rendered with the map. We don't want that, so disable all
	 * React re-rendering of the map (and any children it has) when the Google Map
	 * changes (pan/zoom).
	
	 * @override
	 * @param {*} nextProps 
	 * @param {*} nextState 
	 */
	shouldComponentUpdate(nextProps, nextState) {
		let val = (nextState.initialCenter !== this.state.initialCenter) ;
		return val ;
	}
	
	////////////////////////////////////////////////
	// handlers for Google Maps events recognized by GMR
	onIdle(props, map) {
		console.log("onIdle") ;

		//this the first place where we can access the underlying Google Maps maps object
		//we save it in this object as well as passing it to the app
		if(!this.map) {
			this.props.registerGoogleMap(map) ;
			this.map = map ;
			console.log("The Google Maps Map object has been attached to the SagasodaMap component.") ;

			if(this.props.isGeolocationOn) {
				this.addUserLocationMarker(this.state.initialCenter.lat, this.state.initialCenter.lng) ;
			}
		}
		this.fetchMachinesOnMap(map) ;
	}
	
	onDragend(props, map) {
		console.log("onDragend")

		if(this.isUpdateDisabledByZoom) {
			console.log("No update due to low zoom") ;
		} 

		// if(!this.isUpdateDisabledByZoom) {
			// this.fetchMachinesOnMap(map) ;
		// } 
		// console.log(props, map) ;
		// this.fetchVisibleMarkers(map, this.props.app, this.props.machineListCallback) ;
	}

	onTilesLoaded(a, b) {
		console.log("onTilesLoaded", a, b) ;
	}

	onZoomChanged(_props, map) {
		let zoom = map.getZoom() ;
		console.log("onZoomChanged:", zoom) ;

		this.isUpdateDisabledByZoom = (zoom < this.zoomUpdateLimit) ;
		
		//We update the map on a zoom change if we 
		//we have zoomed out (revealing more of the map at the edges)
		//or we have zoomed in past the threshold
		if(!this.isUpdateDisabledByZoom) {
			if((this.zoom < this.previousZoom) || (this.previousZoom < this.zoomUpdateLimit)) {
				this.fetchMachinesOnMap(map) ;
			}
		}
	}

	onBoundsChanged(props, map) {
 
	}

	/**
	 * @param {} props 
	 * @param {*} marker 
	 * @param {*} ev 
	 */
	onInfoWindowIconClicked(ev) {
		console.log("onInfoWindowIconClicked", ev, this) ;	
		console.log("onInfoWindowIconClicked", ev.target.dataset.markerId, this) ;
		this.markerCallback(ev.target.dataset.markerId) ;
	}

	/**
	 * @override
	 */
	componentDidMount() {
		console.log("SagasodaMap::componentDidMount") ;
		this.props.registerMapComponent(this) ;	
		
		if(!settings.isToolbarFloating) {
			this.adjustMapLayout() ;
		}
	}

	adjustMapLayout() {
		let paletteHeight = document.querySelector(".PaletteWrapper").offsetHeight ;
		let palettePadding = 8 ; //document.querySelector(".Palette").style.padding 
		let newMapHeight = (window.innerHeight - paletteHeight - palettePadding).toString() + "px" ;

		let subDiv1 = document.getElementById("left_pane").firstChild ;
		subDiv1.style.height = newMapHeight ;

		let subDiv2 = subDiv1.firstChild ;
		subDiv2.style.height = newMapHeight ;
		document.querySelector(".PaletteWrapper").style.opacity = "1.0" ;	
	}

	plotMachineMarkers(machines, map) {
		let machineBrands = this.props.machineBrands ;
		let onMarkerClicked = this.onMarkerClicked.bind(this) ;

		machines.forEach(machine => {
			let position = { lat:machine.location.lat, lng:machine.location.lng } ;
			let icon = brandIconPath(machine, machineBrands) ;
			let marker = new this.props.google.maps.Marker(
				{
				position: position,
				map: map,
				icon: icon,
				machine: machine,
				// component: this
				})
			// marker.addListener('click', this.onMarkerClicked) ;
			marker.addListener('click', function(ev) {
				onMarkerClicked(this) ;
			}) ;
			this.cachedMarkers[machine.id] = marker ;
		});
	}

	onMarkerClicked(marker) {
		console.log("SagasodaMap::onMarkerClicked") ;

		let component = this ;
		// let marker = this ;
		// let component = marker.component ;
		// let component = this.mapComponent ;
		let infoWindow = component.buildInfoWindow(marker.machine) ;
		
		infoWindow.addListener("domready", function() {
			let windowElement = this.content.parentElement.parentElement.parentElement ;
			windowElement.classList.add("SagasodaInfoWindow") ;		
		}) ;
		
		// console.log(infoWindow) ;
		infoWindow.open(component.map, marker) ;
		this.putOrientationMarker(marker.machine, component.map) ;
	}

	fetchMachinesOnMap(map) {
		console.log("fetchMachinesOnMap") ;
		let bounds = map.getBounds()  //.toJSON() ;
		let northEast = bounds.getNorthEast() ;
		let southWest = bounds.getSouthWest() ;

		let latN = northEast.lat() ;
		let latS = southWest.lat() ;
		let lngW = southWest.lng() ;
		let lngE = northEast.lng() ;

		//let boundsArr = [latN, latS, lngW, lngE] ;

		;
		// console.log(apiStr) ;
		
		let notifyAppVisibleMarkersChanged = this.props.notifyAppVisibleMarkersChanged ;
		let plotMachineMarkers = this.plotMachineMarkers.bind(this) ;
		let cachedMarkerIds = this.cachedMarkerIds ;
		// let thisComponent = this ;
		
		let url = settings.apiHost + "/api/vendingmachines/"
				+ "?north_of=" + latS
				+ "&south_of=" + latN
				+ "&east_of=" + lngW
				+ "&west_of=" + lngE
				+ "&output=for_map_markers"
		fetch(url)
				.then(function(response){
						// paletteLogo.stopSpin() ;
						return response.json() ;
				})
				.then(function(machines){
					let newMachines = machines.filter(machine => !(cachedMarkerIds.has(machine.id))) ;

					newMachines.forEach(machine => {
						cachedMarkerIds.add(machine.id) ;
					}) ;
					plotMachineMarkers(newMachines, map) ;
					notifyAppVisibleMarkersChanged(machines) ;
				});
	}

	showInfoWindows(machines) {
		this.openedInfoWindows.forEach(infoWindow => infoWindow.close()) ;
		this.openedInfoWindows = [] ;
 
		machines.forEach(machine => {
			let infoWindow = this.buildMachineWithDrinksInfoWindow(machine, machine.vended_drinks_of_drink[0].drink_name) ;
			infoWindow.open(this.map, this.cachedMarkers[machine.id]) ;
			this.openedInfoWindows.push(infoWindow) ;
		});
	}

	buildInfoWindow(machine) {
		let div = document.createElement("div") ;
		div.classList.add("SagasodaInfoWindowContent") ;
		div.classList.add("MachineInfo") ;

		let hed = document.createElement("h5") ;
		hed.innerHTML = brandName(machine, this.props.machineBrands) ;
		div.appendChild(hed) ;
		div.appendChild(buildMachineInfoIconButton(machine.id, this)) ;
	
		let infoWindow = new this.props.google.maps.InfoWindow(
			{
			content: div
			}) ;
		
		return infoWindow ;
	}

	buildMachineWithDrinksInfoWindow(machine, title) {
		let div = document.createElement("div") ;
		div.classList.add("SagasodaInfoWindowContent") ;
		div.classList.add("DrinksList") ;

		let hed = document.createElement("h5") ;
		hed.innerHTML = title ;
		div.appendChild(hed) ;
	
		let dl = document.createElement("dl") ;
		for(var dr of machine.vended_drinks_of_drink) {
			let dt = document.createElement('dt') ;
			dt.innerHTML = dr.volume + 'ml';
			let dd = document.createElement('dd') ;
			dd.innerHTML = '&yen;' + dr.price ;
			dl.appendChild(dt) ;
			dl.appendChild(dd) ;
		}
		div.append(dl) ;
		div.appendChild(buildMachineInfoIconButton(machine.id, this)) ;

		let infoWindow = new this.props.google.maps.InfoWindow(
			{
			content: div
			}) ;
	
		return infoWindow ;
	}

	xxxbuildMachineInfoIconButton(machineId) {
		let img = document.createElement("img") ;
		img.src = "images/ui/buttons/info.png" ;
		img.addEventListener("click", (ev => this.props.notifyAppMarkerSelected(machineId))) ;
		return img ;
	}

	putOrientationMarker(machine, map, isPanTo=false) {
		console.log("addOrientationMarker", machine, map) ;
		const orientationMarkerImage = {
			path: 'M 0,0 10,10 6,10 0,4 -6,10 -10,10 0,0 -10,0 -10,-8 10,-8, 10,0 Z',
			fillColor: '#8fff8f',
			fillOpacity: 1,
			scale: 2,
			strokeColor: '#35df34',
			strokeWeight: 2,
			} ;
		
		let google = this.props.google ;

		let latLng = new google.maps.LatLng(machine.location.lat, machine.location.lng) ;

		orientationMarkerImage.anchor = new google.maps.Point(0, 0) ;
		orientationMarkerImage.origin = new google.maps.Point(0, 0) ;

		if(!this.orientationMarker) {
			this.orientationMarker = new google.maps.Marker({
				map: map,
				icon: orientationMarkerImage,
				position: latLng
			}) ;
		}
		
		this.orientationMarker.setMap(null) ;
		this.orientationMarker.position = latLng;
		this.orientationMarker.icon.rotation = machine.orientation ;
		
		this.orientationMarker.setMap(map) ;
		// console.log("OrientationMarker location", this.orientationMarker.position) ;
		if(isPanTo) {
			this.map.panTo({lat: machine.location.lat, lng: machine.location.lng}) ;	
		}
	}

	addUserLocationMarker(latitude, longitude) {
		console.log("addUserLocationMarker", latitude, longitude) ;
		// console.log("this.map", this.map) ;
		let latLng = new this.props.google.maps.LatLng(latitude, longitude) ;

		if(!this.userLocationMarker) {
			this.userLocationMarker = new this.props.google.maps.Marker({
				map: this.map,
				icon: "images/ui/user-location-marker.png",
				position: latLng
			}) ;
		}
		this.userLocationMarker.setMap(null) ;
		this.userLocationMarker.position = latLng;
		
		this.userLocationMarker.setMap(this.map) ;
		this.map.panTo({lat: latitude, lng: longitude}) ;
	}
}

//I believe that... this effectively exports a component class called SagasodaGoogleMap which includes
//the GoogleApi interface, which was wrapped around the user-defined SagasodaGoogleMap

//This creates a component class SagasodaGoogleMap which implements the GoogleApiWrapper,
//allowing it to respond to Google API events.
export default GoogleApiWrapper({
	apiKey: settings.googleApiKey
	})(SagasodaGoogleMap);
